.image-overlay {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.overlay-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition: all 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transform: perspective(1000px) rotateY(0deg) rotateX(0deg);
  filter: drop-shadow(0 0 0 rgba(0,0,0,0));
}

.overlay-image.hovered {
  transform: perspective(1000px) rotateY(-15deg) rotateX(5deg);
  filter: drop-shadow(20px 20px 30px rgba(0,0,0,0.4));
}

.overlay-filter {
  display: none;
}

/* Hiệu ứng holographic */
.image-overlay::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    linear-gradient(90deg, transparent 0%, rgba(255,255,255,0.1) 25%, transparent 50%, rgba(255,255,255,0.1) 75%, transparent 100%),
    linear-gradient(0deg, transparent 0%, rgba(255,255,255,0.05) 50%, transparent 100%);
  transform: translateX(-100%);
  transition: all 0.8s ease;
  z-index: 1;
  opacity: 0;
  animation: holographic 3s infinite;
}

.image-overlay:hover::before {
  transform: translateX(100%);
  opacity: 1;
}

/* Hiệu ứng magnetic field */
.image-overlay::after {
  content: '';
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  background: 
    radial-gradient(circle at 30% 30%, rgba(0,255,255,0.3) 0%, transparent 50%),
    radial-gradient(circle at 70% 70%, rgba(255,0,255,0.3) 0%, transparent 50%),
    radial-gradient(circle at 50% 50%, rgba(255,255,0,0.2) 0%, transparent 50%);
  z-index: -1;
  opacity: 0;
  transition: all 0.6s ease;
  border-radius: 8px;
  animation: magneticField 4s infinite;
}

.image-overlay:hover::after {
  opacity: 1;
}

@keyframes holographic {
  0%, 100% { opacity: 0; }
  50% { opacity: 1; }
}

@keyframes magneticField {
  0%, 100% { 
    transform: scale(1) rotate(0deg); 
    filter: hue-rotate(0deg);
  }
  33% { 
    transform: scale(1.05) rotate(120deg); 
    filter: hue-rotate(120deg);
  }
  66% { 
    transform: scale(1.05) rotate(240deg); 
    filter: hue-rotate(240deg);
  }
} 