body {
  background: #1f1f1f;
  background-image: 
    radial-gradient(circle at 25% 25%, rgba(255,255,255,0.04) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(255,255,255,0.04) 0%, transparent 50%);
  color: #fff;
  font-family: 'SF Pro Display', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

main {
  max-width: 90vw;
  margin: 0;
  margin-left: 0;
  padding: 0 8vw 0 8vw;
  text-align: left;
}

@media (max-width: 768px) {
  main {
    padding: 0 4vw;
  }
}

section {
  padding: 100px 0;
  position: relative;
}

section:not(:last-child)::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
}

.section-heading {
    display: flex;
    align-items: center;
    font-size: 32px;
    margin-bottom: 40px;
}

.section-heading span {
    margin-right: 15px;
    color: #aaa;
    font-size: 24px;
    font-weight: normal;
}

/* Loading Overlay Styles */
.loading-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #1f1f1f;
    background-image: 
        radial-gradient(circle at 25% 25%, rgba(255,255,255,0.04) 0%, transparent 50%),
        radial-gradient(circle at 75% 75%, rgba(255,255,255,0.04) 0%, transparent 50%);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    opacity: 1;
    visibility: visible;
    transition: opacity 0.75s ease-out, visibility 0.75s ease-out;
}

.loading-overlay.fading {
    opacity: 0;
    visibility: hidden;
}

.loading-content {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    position: relative;
    z-index: 2;
}

.loading-logo {
    width: 38px;
    height: 38px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Vòng tròn bên ngoài quay ngược chiều kim đồng hồ */
.loading-logo::before {
    content: '';
    position: absolute;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-top: 2px solid rgba(255, 255, 255, 0.8);
    border-radius: 50%;
    animation: spinCounterClockwise 1.5s linear infinite;
}

/* Vòng tròn bên trong quay ngược chiều kim đồng hồ */
.loading-logo::after {
    content: '';
    position: absolute;
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-right: 1px solid rgba(255, 255, 255, 0.6);
    border-radius: 50%;
    animation: spinCounterClockwise 1.125s linear infinite;
}

.loading-logo img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    filter: drop-shadow(0 0 6px rgba(255, 255, 255, 0.2));
    animation: spinClockwise 1.5s linear infinite, float 3s ease-in-out infinite;
}

@keyframes spinClockwise {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

@keyframes spinCounterClockwise {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(-360deg);
    }
}

@keyframes float {
    0%, 100% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-3px);
    }
}

/* Website Content */
.website-content {
    opacity: 0;
    transition: opacity 0.75s ease-in;
}

.website-content.show {
    opacity: 1;
}

/* Container wrappers */
.content-wrapper,
.hero-wrapper {
    max-width: 1050px;
    margin: 0 auto;
    padding: 0 150px;
}

/* Responsive Design - Desktop First Approach */
@media (max-width: 1200px) {
    .content-wrapper,
    .hero-wrapper {
        max-width: 1000px;
        padding: 0 120px;
    }
}

@media (max-width: 1024px) {
    .content-wrapper,
    .hero-wrapper {
        max-width: 900px;
        padding: 0 100px;
    }
}

@media (max-width: 900px) {
    .content-wrapper,
    .hero-wrapper {
        max-width: 800px;
        padding: 0 80px;
    }
}

@media (max-width: 768px) {
    .loading-logo {
        width: 29px;
        height: 29px;
    }
    
    .content-wrapper,
    .hero-wrapper {
        max-width: 100%;
        padding: 0 60px;
    }
    
    .section-heading {
        font-size: 28px;
    }
    
    .section-heading span {
        font-size: 20px;
    }
}

@media (max-width: 600px) {
    .content-wrapper,
    .hero-wrapper {
        padding: 0 40px;
    }
}

@media (max-width: 480px) {
    .loading-logo {
        width: 24px;
        height: 24px;
    }
    
    .content-wrapper,
    .hero-wrapper {
        padding: 0 25px;
    }
    
    .section-heading {
        font-size: 24px;
    }
    
    .section-heading span {
        font-size: 18px;
    }
}

@media (max-width: 360px) {
    .content-wrapper,
    .hero-wrapper {
        padding: 0 20px;
    }
}
