@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap');

#work {
  padding: 100px 0;
  color: #fff;
}

.section-heading {
  font-size: 20px;
  font-family: 'Inter', sans-serif;
  margin-bottom: 50px;
  text-align: left;
  position: relative;
}

.projects-container {
  display: flex;
  flex-direction: column;
  gap: 100px;
}

.project-card {
  display: flex;
  align-items: center;
  gap: 20px;
}

.project-card.reversed {
  flex-direction: row-reverse;
}

.project-content {
  flex: 1;
  max-width: 50%;
  text-align: left;
}

.project-card:nth-child(even) .project-content {
  text-align: left;
}

.project-overline {
  color: #00A9FF;
  font-size: 0.8rem;
  font-style: poppins, sans-serif;
  letter-spacing: 1px;
}

.project-title {
  font-family: 'Inter', sans-serif;
  font-size: 28px;
  font-weight: 550;
  margin-top: 10px;
  margin-bottom: 20px;
}

.project-title a {
  color: #fff;
  text-decoration: none;
}

.project-description {
  background-color: #1a2b3c;
  padding: 20px;
  border-radius: 0px;
  margin-bottom: 30px;
  width: 510px; 
  max-width: 100%; 
  box-sizing: border-box; 
}

.project-tags {
  display: flex;
  justify-content: flex-end;
  gap: 15px;
  list-style: none;
  padding: 5px;
  margin-bottom: 40px;
  font-style: poppins, sans-serif;
  letter-spacing: 1.3px;
}

.project-card:nth-child(even) .project-tags {
  justify-content: flex-start;
}

.project-tags li {
  font-size: 0.8rem;
  color: #8892b0;
}

.project-image {
  flex: 1;
}

.project-image img {
  width: 90%; 
  border-radius: 0;
}

.project-image.align-right {
  margin-left: auto;
  margin-right: 0;
  display: flex;
  justify-content: flex-end;
}

.project-image.align-right img {
  width: 100%;
  max-width: 500px;
  display: block;
}

.tools-section {
  margin-top: 80px;
  text-align: center;
}

.tools-caption {
  font-size: 0.9rem;
  color: #a0a0a0;
  margin-bottom: 30px;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  font-style: italic;
}

.tools-heading {
  font-size: 1.2rem;
  color: #00A9FF;
  letter-spacing: 1px;
  margin-bottom: 50px;
}

.tools-logos {
  display: flex;
  flex-wrap: wrap; 
  justify-content: center; 
  gap: 30px; 
  max-width: 900px; 
  margin: auto; 
}

.tools-logos img {
  width: 60px;  
  height: auto;
  object-fit: contain;
}

.tools-logos img:hover {
  transform: scale(1.1);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .project-description {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .project-card {
    flex-direction: column;
  }

  .project-card.reversed {
    flex-direction: column;
  }

  .project-content {
    max-width: 100%;
    text-align: left;
  }

  .project-tags {
    justify-content: flex-start;
  }
  
  .project-card:nth-child(even) .project-tags {
    justify-content: flex-start;
  }
  
  .project-image {
    width: 100%;
  }
  
  .project-image img {
    width: 100%;
  }
  
  .project-image.align-right img {
    width: 100%;
    max-width: 100%;
  }
  
  .tools-logos {
    gap: 20px;
  }
  
  .tools-logos img {
    width: 50px;
  }
}

@media (max-width: 480px) {
  .project-title {
    font-size: 24px;
  }
  
  .project-description {
    padding: 15px;
  }
  
  .tools-logos {
    gap: 15px;
  }
  
  .tools-logos img {
    width: 40px;
  }
}
