@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap');
#about {
  padding: 100px 0;
  color: #fff;
}

.about-container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 4vw;
  width: 100%;
}

.about-text {
  max-width: 50%;
  flex: 1;
}

.about-image {
  width: 31.25vw; /* Tăng từ 25vw lên 31.25vw (1.25 lần) */
  height: 31.25vw;
  max-width: 375px; /* Tăng từ 300px lên 375px (1.25 lần) */
  max-height: 375px;
  flex-shrink: 0;
}

.about-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border: 2px solid rgba(255, 255, 255, 0.1);
}

/* Responsive Design */
@media (max-width: 768px) {
  .about-container {
    flex-direction: column;
    gap: 30px;
  }
  
  .about-text {
    max-width: 100%;
  }
  
  .about-image {
    width: 300px; /* Tăng từ 200px lên 300px (1.5 lần) */
    height: 300px;
    align-self: center;
  }
}

@media (max-width: 480px) {
  .about-image {
    width: 225px; /* Tăng từ 150px lên 225px (1.5 lần) */
    height: 225px;
  }
}

/* iPhone 12 Pro specific adjustments */
@media (max-width: 390px) {
  .about-image {
    width: 200px;
    height: 200px;
  }
}
