#experience {
  padding: 100px 0; /* Loại bỏ padding 8vw, chỉ giữ padding dọc */
  color: #fff;
}

.job-role {
  color: #aaa;
  font-style: poppins, sans-serif;
  letter-spacing: 1.5px;
  white-space: nowrap; 
}

.company-title {
  font-size: 1.3rem;
  font-weight: 500;
  color: #00A9FF;
  letter-spacing: 0.5px;
  margin-bottom: 0.8rem;
  word-wrap: break-word;
  overflow-wrap: break-word;
  hyphens: auto;
}

.job-meta {
  display: flex;
  align-items: baseline;
  gap: 8px; 
  margin-bottom: 0.3rem;
}

.range {
  font-size: 16px;
  color: #aaa; 
  margin-bottom: 0.7rem;
  font-style: poppins, sans-serif;
  letter-spacing: 1.5px;
}

.job-desc,
ul.job-tasks,
ul.triangle-list {
  max-width: 50%;
  width: 100%;
  margin-bottom: 0.5rem;
  text-align: left;
  margin-left: 0;
  margin-right: 0;
  flex-basis: 0%;
  flex-grow: 1;
  flex-shrink: 1;
}

ul.job-tasks,
ul.triangle-list {
  margin: 0;
  padding-left: 1.2em;
  max-width: 50%;
  margin-left: auto;
  margin-right: 0;
}

ul.triangle-list li,
ul.job-tasks li {
  margin-bottom: 0.7em;
  list-style-type: none;
  position: relative;
  padding-left: 1.3em;
}

/* Tam giác đầu dòng */
ul.triangle-list li::before,
ul.job-tasks li::before {
  content: "▶";
  position: absolute;
  left: 0;
  color: #00A9FF;
  font-size: 0.60em;
  top: 0.1em;
}

@media (max-width: 900px) {
  .job-desc, ul.job-tasks, ul.triangle-list {
    max-width: 100%;
  }
  
  .company-title {
    font-size: 1.1rem;
    line-height: 1.3;
  }
}

@media (max-width: 480px) {
  .company-title {
    font-size: 1rem;
    line-height: 1.4;
  }
  
  .job-role {
    font-size: 0.9rem;
  }
}

/* iPhone 12 Pro specific adjustments */
@media (max-width: 390px) {
  .company-title {
    font-size: 0.95rem;
    line-height: 1.5;
  }
  
  .job-role {
    font-size: 0.85rem;
  }
}