header {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 15px 40px 15px 20px; /* Giảm padding phải để đẩy menu sát mép */
  background-color: #1a1a1a;
  z-index: 100;
  transition: top 0.3s;
  display: flex;
  align-items: center;
  justify-content: flex-end; /* Căn phải */
  box-sizing: border-box; /* Đảm bảo padding tính đúng */
}

nav {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: auto;
}

nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  margin-right: 0; 
}

nav ul li {
  margin: 0 25px; 
}

nav ul li a {
  color: #fff;
  text-decoration: none;
  font-size: 12px;
  font-family: 'Poppins', sans-serif; 
  letter-spacing: 1.5px;
  transition: color 0.3s;
  cursor: pointer;
}

nav ul li a:hover {
  color: #00A9FF;
}

@media (max-width: 768px) {
  header {
    padding: 15px 20px; /* Giảm padding để phù hợp màn hình nhỏ */
  }
  nav ul {
    margin-right: 20px;
  }
  nav ul li {
    margin: 0 10px;
  }
  nav ul li a {
    font-size: 10px;
  }
}
