#whatsnext {
  padding: 100px 8vw;
  text-align: center;
  margin-top: 60px; /* Spacing above the section */
}

#whatsnext h2 {
    font-size: 32px; /* Adjusted to match other section headings */
    font-weight: normal;
    margin-bottom: 60px; /* Added spacing */
    display: flex;
    justify-content: center; /* Center the heading */
    align-items: center;
}

#whatsnext h2 span {
    margin-right: 15px;
    color: #aaa;
    font-size: 24px;
    font-weight: normal;
}

#whatsnext p {
    margin-bottom: 50px; /* Spacing below paragraph */
}

.contact-button-whatsnext {
  border: 1px solid #00A9FF;
  padding: 10px 20px;
  color: #00A9FF;
  text-decoration: none;
  border-radius: 5px;
  transition: all 0.3s ease;
  display: inline-block; /* Allows padding and margin */
  margin-top: 20px; /* Spacing above button */
  margin-bottom: 30px;
}

.contact-button-whatsnext:hover {
  background-color: #00A9FF;
  color: #fff;
}
