.footer {
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 100%);
  border-top: 1px solid #333;
  padding: 40px 0;
  margin-top: 80px;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 8vw;
  text-align: center;
}

.footer-text {
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: center;
}

.copyright {
  font-size: 18px;
  font-weight: 600;
  color: #ffffff;
  letter-spacing: 0.5px;
}

.tagline {
  font-size: 16px;
  color: #888;
  font-style: italic;
  margin: 4px 0;
}

.version {
  font-size: 14px;
  color: #666;
  font-family: 'Courier New', monospace;
  letter-spacing: 0.3px;
}

/* Hover effects */
.footer:hover .copyright {
  color: #64ffda;
  transition: color 0.3s ease;
}

.footer:hover .tagline {
  color: #aaa;
  transition: color 0.3s ease;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .footer-content {
    max-width: 1000px;
  }
}

@media (max-width: 1024px) {
  .footer-content {
    max-width: 900px;
  }
  
  .footer {
    padding: 35px 0;
    margin-top: 70px;
  }
}

@media (max-width: 900px) {
  .footer-content {
    max-width: 800px;
  }
  
  .copyright {
    font-size: 17px;
  }
  
  .tagline {
    font-size: 15px;
  }
  
  .version {
    font-size: 13px;
  }
}

@media (max-width: 768px) {
  .footer-content {
    padding: 0 6vw;
  }
  
  .footer {
    padding: 30px 0;
    margin-top: 60px;
  }
  
  .footer-text {
    gap: 6px;
  }
  
  .copyright {
    font-size: 16px;
  }
  
  .tagline {
    font-size: 14px;
  }
  
  .version {
    font-size: 12px;
  }
}

@media (max-width: 600px) {
  .footer-content {
    padding: 0 5vw;
  }
  
  .footer {
    padding: 25px 0;
    margin-top: 50px;
  }
  
  .copyright {
    font-size: 15px;
  }
  
  .tagline {
    font-size: 13px;
  }
  
  .version {
    font-size: 11px;
  }
}

@media (max-width: 480px) {
  .footer-content {
    padding: 0 4vw;
  }
  
  .footer {
    padding: 20px 0;
    margin-top: 40px;
  }
  
  .footer-text {
    gap: 4px;
  }
  
  .copyright {
    font-size: 14px;
  }
  
  .tagline {
    font-size: 12px;
  }
  
  .version {
    font-size: 10px;
  }
}

@media (max-width: 360px) {
  .footer-content {
    padding: 0 3vw;
  }
  
  .footer {
    padding: 18px 0;
    margin-top: 35px;
  }
  
  .copyright {
    font-size: 13px;
  }
  
  .tagline {
    font-size: 11px;
  }
  
  .version {
    font-size: 9px;
  }
} 