#hero {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  text-align: left;
  padding: 0;
}

.hero-content {
  max-width: 800px;
}

.hero-content .intro {
  font-size: 24px;
  color: #aaa;
}

.hero-content h1 {
  font-size: 72px;
  margin: 10px 0;
  white-space: nowrap;
}

.hero-content h2 {
  font-size: 48px;
  color: #aaa;
  margin-bottom: 30px;
}

.contact-button {
  display: inline-block;
  padding: 15px 30px;
  border: 2px solid #fff;
  color: #fff;
  text-decoration: none;
  font-size: 20px;
  border-radius: 8px;
  transition: background-color 0.3s, color 0.3s;
}

.contact-button:hover {
  background-color: #fff;
  color: #111;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .hero-content h1 {
    font-size: 64px;
  }
  
  .hero-content h2 {
    font-size: 42px;
  }
}

@media (max-width: 900px) {
  .hero-content h1 {
    font-size: 56px;
  }
  
  .hero-content h2 {
    font-size: 36px;
  }
  
  .hero-content .intro {
    font-size: 20px;
  }
}

@media (max-width: 768px) {
  #hero {
    justify-content: center;
    text-align: center;
  }

  .hero-content h1 {
    font-size: 48px;
    white-space: normal;
  }

  .hero-content h2 {
    font-size: 32px;
  }

  .hero-content .intro {
    font-size: 18px;
  }

  .contact-button {
    font-size: 16px;
    padding: 12px 24px;
    margin-bottom: 20px; /* Tạo khoảng cách với social links */
  }
}

@media (max-width: 480px) {
  .hero-content h1 {
    font-size: 36px;
  }

  .hero-content h2 {
    font-size: 24px;
  }

  .hero-content .intro {
    font-size: 16px;
  }

  .contact-button {
    font-size: 14px;
    padding: 10px 20px;
    margin-bottom: 15px; /* Giảm khoảng cách trên mobile nhỏ */
  }
}

@media (max-width: 360px) {
  .hero-content h1 {
    font-size: 32px;
  }

  .hero-content h2 {
    font-size: 20px;
  }
}

/* iPhone 12 Pro specific adjustments */
@media (max-width: 390px) {
  .hero-content h1 {
    font-size: 34px;
  }

  .hero-content h2 {
    font-size: 22px;
  }

  .contact-button {
    margin-bottom: 12px;
  }
}
