.social-links {
  position: fixed;
  left: 1.5vw;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  z-index: 1000;
}

.social-links a {
  margin: 12px 0;
  font-size: 28px;
  color: #fff;
  transition: all 0.3s ease;
  padding: 8px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.social-links a:hover {
  color: #fff;
  background: rgba(255, 255, 255, 0.1);
  animation: bounce 0.6s ease;
  box-shadow: 0 4px 12px rgba(255, 255, 255, 0.2);
}

@keyframes bounce {
  0%, 20%, 60%, 100% { 
    transform: translateY(0); 
  }
  40% { 
    transform: translateY(-10px); 
  }
  80% { 
    transform: translateY(-5px); 
  }
}

/* Mobile responsive - chuyển social links xuống dưới trên mobile */
@media (max-width: 768px) {
  .social-links {
    position: static;
    transform: none;
    flex-direction: row;
    justify-content: center;
    gap: 20px;
    margin-top: 40px;
    padding: 20px 0;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  
  .social-links a {
    margin: 0;
    font-size: 24px;
    padding: 12px;
    transition: all 0.3s ease;
  }
  
  .social-links a:hover {
    background: rgba(255, 255, 255, 0.15);
    transform: translateY(-2px);
  }
}

@media (max-width: 480px) {
  .social-links {
    gap: 15px;
    margin-top: 30px;
    padding: 15px 0;
  }
  
  .social-links a {
    font-size: 22px;
    padding: 10px;
  }
}

/* iPhone 12 Pro specific adjustments */
@media (max-width: 390px) {
  .social-links {
    gap: 12px;
    margin-top: 25px;
    padding: 12px 0;
  }
  
  .social-links a {
    font-size: 20px;
    padding: 8px;
  }
}